* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

li {
  list-style: none;
}

ul {
  padding: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.list-item {
  display: flex;
  align-items: center;
}

.list-item > img {
  margin-right: 10px;
}

.thumb {
  display: flex;
  align-items: center;
}

.thumb > img {
  margin-right: 10px;
}


.info_item > span{
  font-weight: bold;
}